@import url(https://fonts.googleapis.com/css2?family=Mohave:ital,wght@1,300&family=Stylish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-header{
  text-align: center;
  padding: 20px;
}

.header{
  font-family: 'Stylish', sans-serif;
  font-weight: bold;
  font-size: 50px;
}

.app-body{
  padding: 20px;
  text-align: center;
}

.body-app{
  background-image: url(../../static/media/background.1024404b.png);
}

.language-select{
  height: 40px !important;
  margin-bottom: 15px;
  outline: none !important;
}

.form__group:not(:last-child) {
  margin-bottom: 2rem;
}
.form__input:focus {
  outline: none;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
  border-bottom: 7px solid #55c57a;
}


